import React, { useState } from 'react';
import classNames from 'classnames';
import { Good } from './components/Good';
import 'bulma/css/bulma.css';
import './App.scss';

export const goodsFromServer = [
  'Dumplings',
  'Carrot',
  'Eggs',
  'Ice cream',
  'Apple',
  'Bread',
  'Fish',
  'Honey',
  'Jam',
  'Garlic',
];

enum SortType {
  NONE,
  ALPHABET,
  LENGTH,
}

type ReorderOptions = {
  sortType: SortType;
  isReversed: boolean;
};

export function getReorderedGoods(
  goods: string[],
  { sortType, isReversed }: ReorderOptions,
) {
  const visibleGoods = [...goods];

  switch (sortType) {
    case SortType.ALPHABET:
      visibleGoods.sort();
      break;

    case SortType.LENGTH:
      visibleGoods.sort((firstGood, secondGood) => {
        return firstGood.length - secondGood.length;
      });
      break;

    default:
      break;
  }

  if (isReversed) {
    visibleGoods.reverse();
  }

  return visibleGoods;
}

export const App: React.FC = () => {
  const [isReversed, setReversed] = useState(false);
  const [sortType, setSortType] = useState(SortType.NONE);
  const visibleGoods = getReorderedGoods(goodsFromServer, {
    isReversed,
    sortType,
  });
  const isSortedByAlphabet = sortType === SortType.ALPHABET;
  const isSortedByLength = sortType === SortType.LENGTH;
  const isInitial = !isReversed && sortType === SortType.NONE;

  const setOppositeReverse = () => {
    setReversed(current => !current);
  };

  const resetAll = () => {
    setReversed(false);
    setSortType(SortType.NONE);
  };

  return (
    <div className="section content">
      <div className="buttons">
        <button
          type="button"
          className={classNames(
            'button',
            'is-info', {
              'is-light': !isSortedByAlphabet,
            },
          )}
          onClick={() => setSortType(SortType.ALPHABET)}
        >
          Sort alphabetically
        </button>

        <button
          type="button"
          className={classNames(
            'button',
            'is-success', {
              'is-light': !isSortedByLength,
            },
          )}
          onClick={() => setSortType(SortType.LENGTH)}
        >
          Sort by length
        </button>

        <button
          type="button"
          className={classNames(
            'button',
            'is-warning', {
              'is-light': !isReversed,
            },
          )}
          onClick={setOppositeReverse}
        >
          Reverse
        </button>

        {!isInitial && (
          <button
            type="button"
            className="button is-danger is-light"
            onClick={resetAll}
          >
            Reset
          </button>
        )}
      </div>

      <ul>
        {visibleGoods.map((good) => (
          <li key={good}>
            <Good name={good} />
          </li>
        ))}
      </ul>
    </div>
  );
};
